import React from 'react';
import { getAffiliateById } from '../../../../config/affiliate';

const affiliateIdFormatter = (props) => {
    let cell = props.value;
    return <div>{getAffiliateById(cell).label || ""}</div>
}

export const affiliateIdFilter = (filter, row) => {
    return String(getAffiliateById(row[filter.id]).label || "").toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())
}

export default affiliateIdFormatter