import {
    UPDATE_CURRENT_STATUS_SUCCESS,
    MODIFIED_HISTORY_SUCCESS,
    UPDATE_URGENT_DELIVERY_STATUS_SUCCESS,
    UPDATE_URGENT_DELIVERY_STATUS_ERROR
} from './constants';
import { getModifiedHistoryByOrder, updateCurrentStatus, updateUrgentDeliveryStatus } from '../../api';
import { showLoader, hideLoader } from '../../components/Loader/action';
import handleError from '../../errorHandler';
import { showPopup } from '../../components/Popup/action';
import { toast } from 'react-toastify';

export function getMoreModifiedHistoryByOrder(id, affiliate_id) {
    return (dispatch) => {
        dispatch(showLoader());
        getModifiedHistoryByOrder(id, affiliate_id)
            .then(response => {
                dispatch(hideLoader());
                // dispatch(showPopup({ type: "modifiedHistory", header: "Order Modifications", data: response.data }));
                // dispatch(successModifiedHistory({ header: "Order Modifications", modificationHistory: response.data }));
                if (Array.isArray(response.data)) {
                    dispatch(showPopup({ type: "modifiedHistory", header: "Order Modifications", data: response.data }));
                } else {
                    toast.error(response.data.message || "Something Went Wrong!");
                }
            })
            .catch(error => {
                handleError(error, dispatch)
            });
    };
}

export function successModifiedHistory(data) {
    return {
        type: MODIFIED_HISTORY_SUCCESS,
        data
    }
}


export function submitCurrentStatus(role, data) {
    return (dispatch) => {
        dispatch(showLoader());
        updateCurrentStatus(role, data)
            .then(response => {
                dispatch(hideLoader());
                dispatch(submitCurrentStatusSuccess(response.data));
                toast.success(response.data.message)
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    };
}

export function submitCurrentStatusSuccess(data) {
    return {
        type: UPDATE_CURRENT_STATUS_SUCCESS,
        data
    }
}

export function UrgentDeliveryStatus(role, data) {
    return (dispatch) => {
        dispatch(showLoader());
        updateUrgentDeliveryStatus(role, data)
            .then(response => {
                dispatch(hideLoader());
                dispatch(updateUrgentDeliveryStatusSuccess(response.data));
                toast.success(response.data.message)
            })
            .catch(error => {
                handleError(error, dispatch);
                dispatch(updateUrgentDeliveryStatusError(error.response));
            });
    };
}

export function updateUrgentDeliveryStatusSuccess(data) {
    return {
        type: UPDATE_URGENT_DELIVERY_STATUS_SUCCESS,
        data
    }
}

export function updateUrgentDeliveryStatusError(data) {
    return {
        type: UPDATE_URGENT_DELIVERY_STATUS_ERROR,
        data
    }
}
