import React, { Component } from 'react';
// import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import "./style.scss";
import { getProductNameBySKU } from '../../../../config/products';
import { getDateTime } from '../../../../config/time';
import { convertCurrency } from '../../../../utilities/commaSeperation';
// import { CancelOrderOrCart } from '../OrderId/components/cancelOrderCart';
import CollapsableWidget from '../../../../components/CollapsableWidget';
import { getRefundsListAndCollectBankDetails } from '../../../FinanceModule/components/commonComponents/RefundAndCollectBankDetails';
import { getRoleByUserDetails } from '../../../../utilities/getRoleByUserDetails';

const possibleCartEditStatus = [1, 2, 3, 5, 6, 14, 18, 19];
// const possibleCartEditRoles = ["OC", "OPS"];
const splitOrderStatus = [1, 2, 3, 5, 6, 14, 18, 19];
// const splitOrderItemSku = ["WDCM", "WOMFM", "WSWB"];

class CartDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showModificationIndex: -1
        }
    }

    showModificationDetails = (row) => {
        this.setState({ showModificationIndex: this.state.showModificationIndex !== -1 ? -1 : row.index });
    }

    getModifiedHistoryByOrder = (rowInfo) => {
        return <div className='cart-details1'>
            <div className="product-name"><b>Reason : </b> {rowInfo.addon_reason} {rowInfo.addon_reason_txt}</div>
            <div className="dimensions"><b>Net Amount : </b> {convertCurrency(rowInfo.addon_effective_price)}</div>
            {rowInfo.addon_comments &&
                <div className=""><b>Modification Comment : </b> {rowInfo.addon_comments}</div>
            }
            <div className="qty"><b>Modified By : </b> {rowInfo.modified_by || ""}</div>
            <div className="price"><b>Timestamp : </b> {getDateTime(rowInfo.addon_created_datetime)}</div>
            {rowInfo &&
                <div className="custom-badge"
                    onClick={() => this.props.getMoreModifiedHistoryByOrder(
                        rowInfo.id,
                        rowInfo.affiliate_id,
                    )}
                >More</div>
            }
        </div>
    }

    cancelOrder = () => {
        let Check = window.confirm("Are you sure want to Cancel?");
        console.log(Check);
        if (Check) {
            let rowInfo = this.props.rowInfo.original;
            let payload = {
                affiliate_id: rowInfo.affiliate_id || 0,
                cart_id: rowInfo.id
            };
            this.props.cancelOrderOrCart(payload);
        }
    }

    splitOrder = (rowInfo) => {
        let Check = window.confirm("Are you sure want to Split Order?");
        console.log(Check, rowInfo);
        if (Check) {
            let payload = {
                affiliate_id: rowInfo.affiliate_id,
                cart_id: rowInfo.id,
                orm_order_status: rowInfo.orm_order_status,
                item_quantity: rowInfo.item_quantity,
            };
            console.log("split-order", payload)
            this.props.applySplitOrder && this.props.applySplitOrder(payload);
        }
    }

    applyPriceDropWithContent = (row) => {
        let Check = window.confirm("Are you sure want to do this change?");
        if (Check) {
            let data = {
                affiliate_id: row.affiliate_id || 0,
                cart_id: row.id,
                updated_by_role: getRoleByUserDetails(this.props.userDetails),
                updated_by_id: this.props.userDetails.login_id
            };
            this.props.applyPriceDropRefund(data);
        }
    }

    getDiscount = (rowInfo) => {
        let sku = rowInfo.item_sku || "";      // add rowInfo.coupon_accessories_percentage when it is required
        return (sku.includes("WOMFM") || sku.includes("WDCM")) ?
            rowInfo.coupon_percentage :
            sku.includes("WSWB") ?
                rowInfo.coupon_cot_percentage : rowInfo.coupon_accessories_percentage ? rowInfo.coupon_accessories_percentage : "";
    }

    getFlatDiscount = (rowInfo) => {
        let discount = rowInfo.coupon_flat_discount;

        if (this.props.allOrders) {
            let totalPrice = 0;
            this.props.allOrders.filter((cart) => cart.cart_id === rowInfo.cart_id).map(cart => {
                totalPrice += Number(cart.item_price) + Number(cart.item_quantity);
                return "";
            });

            discount = (Number(rowInfo.item_price) * Number(rowInfo.item_quantity) * Number(rowInfo.coupon_flat_discount)) / totalPrice;
            // console.log("Discount: ", discount);
        }
        return discount;
    }

    getDiscountAmountText = (rowInfo) => {
        if (rowInfo.coupon_flat_discount && ![0, '0'].includes(rowInfo.coupon_flat_discount)) {
            // if (Number(rowInfo.coupon_type) === 2) {
            return convertCurrency(this.getFlatDiscount(rowInfo));
        } else {
            // } else if ((Number(rowInfo.coupon_type) === 1) || rowInfo.coupon_discount_amount) {
            return (this.getDiscount(rowInfo) + '%' + (rowInfo.coupon_discount_amount ? (' -> ' + convertCurrency(rowInfo.coupon_discount_amount)) : ''));
        }
    }

    getDiscountAmount = (rowInfo) => {
        if ((Number(rowInfo.coupon_type) === 1) || rowInfo.coupon_discount_amount) {
            return rowInfo.coupon_discount_amount;
            // } else if (Number(rowInfo.coupon_type) === 2) {
        } else if (rowInfo.coupon_flat_discount) {
            return this.getFlatDiscount(rowInfo);
        }
        return 0;
    }

    render() {
        let row = this.props.rowInfo.original;
        let { userDetails, activePlatform } = this.props;
        // let discount = Number(row.coupon_id) !== 0 && this.getDiscount(row);
        // console.log(discount)
        // console.log(this.props)
        // var cartEditRole = this.props.userDetails.role &&
        //     possibleCartEditRoles.some((role) => userDetails.role.includes(role))
        //     &&
        //     possibleCartEditRoles.filter(item => (userDetails.role).includes(item)
        //     );
        //   console.log("ROLE", cartEditRole);
        var style = {
            float: "right"
        };
        return <div id={`cart-details-orderid-${row && row.order_id}-cartid-${row && row.id}`} className="cart-details break-word">
            {window.location &&
                window.location.pathname &&
                window.location.pathname.includes("refund-list/customization-orders") &&
                row.delivered_timestamp &&
                <div className="custom-badge">Order Delivered</div>
            }

            {userDetails && userDetails.role &&
                (
                    userDetails.role.includes("TEAML") ||
                    (!row.shipment_timestamp
                        // && possibleCartEditRoles.some((role) => userDetails.role.includes(role))
                        && possibleCartEditStatus.includes(row.orm_order_status))    //  Cart orm_order_status
                ) &&
                row.order_type === 0 &&      //  initial order
                <div style={style}>
                    {/* <Link to={`/cart-edit/${row.affiliate_id}/${row.id}/?role=${cartEditRole}`}> */}
                    <a href={`/cart-edit/${row.affiliate_id}/${row.id}/`} target="_blank"
                        without rel="noopener noreferrer">
                        <i className="fa fa-pencil-square-o font color" aria-hidden="true" ></i>
                    </a>
                </div>
            }

            <div className="product-name"><b>Product Name : </b> {
                activePlatform && activePlatform.slug && activePlatform.slug === "affiliate_fba" ?
                    row.item_name :
                    getProductNameBySKU(row.addon_item_sku || row.item_sku, row.item_sku)}
            </div>

            {row.addon_dimensions ? <div>
                <div className="sku"><b>Dimensions : </b> <div className="line-through-text" > {row.item_dimensions}</div></div>
                <div className="sku"> {row.addon_dimensions}</div>
            </div>
                : row.item_dimensions && <div className="sku"><b>Dimensions : </b> {row.item_dimensions}</div>
            }
            {row.addon_item_sku ? <div>
                <div className="sku break-word"><b>SKU : </b><div className="line-through-text" > {row.item_sku}</div></div>
                <div className="sku break-word"> {row.addon_item_sku}</div>
            </div>
                : <div className="sku break-word"><b>SKU : </b> {row.item_sku}</div>
            }
            <div className="qty"><b>Qty : </b> {row.item_quantity}</div>
            {row.order_type === 2 && row.extra_details && row.extra_details.extra_price ?
                <div className="price"><b>Extra Price : </b> {convertCurrency(row.extra_details.extra_price)}</div>
                : row.order_type !== 2 && <div className="price"><b>Price : </b> {convertCurrency(row.item_price)}</div>
            }

            {Number(row.coupon_id || 0) !== 0 &&
                // row.item_price && (row.item_sku || row.addon_item_sku) &&
                <div className="price"><b>Discount : </b>{this.getDiscountAmountText(row)}</div>}

            {(row.order_type === 2 && row.extra_details && row.extra_details.extra_price ?
                <div className="total">
                    <b>Total : </b> INR {convertCurrency(row.extra_details.extra_price)} * {row.item_quantity} = {convertCurrency(Number(row.extra_details.extra_price) * Number(row.item_quantity))}
                </div>
                : row.order_type !== 2 && <div>
                    {/* {row.coupon_flat_discount && <div className="price"><b>Discount : </b>  {convertCurrency(row.coupon_flat_discount)}</div>}
                    {row.coupon_percentage && <div className="price"><b>Discount (%) : </b> {row.coupon_percentage}</div>} */}

                    {row.addon_effective_price ?
                        <div>
                            <div className="total"><b>Total : </b> INR {convertCurrency(row.item_price)} * {row.item_quantity} {Number(row.coupon_id || 0) !== 0 && (' - ' + convertCurrency(this.getDiscountAmount(row)))} =
                                {row.net_price && <div className="modify">{` ${convertCurrency(row.net_price)}`}</div>}
                                <div className="effectivePrice"> {convertCurrency(row.addon_effective_price || 0)}</div>
                            </div>
                        </div>
                        :
                        <div className="total"><b>Total : </b> INR {convertCurrency(row.item_price)} * {row.item_quantity} {Number(row.coupon_id || 0) !== 0 && (' - ' + convertCurrency(this.getDiscountAmount(row)))}= {
                            row.net_price ? convertCurrency(row.net_price) : convertCurrency(Number(row.item_price || 0) * Number(row.item_quantity || 0) - Number(Number(row.coupon_id) !== 0 ? this.getDiscountAmount(row) : 0))
                        }
                        </div>
                    }
                </div>)}
            {/* <div className="total"><b>Total: </b> INR {row.item_price} * {row.item_quantity} {row.coupon_flat_discount && `- ${row.coupon_flat_discount}`} =
                    {<div className={row.addon_effective_price ? "modify" : "effectivePrice"}>{`  ${row.net_price || Number(row.item_price) * Number(row.item_quantity)}`}</div>}
                {row.addon_effective_price && <div className="effectivePrice"> {row.addon_effective_price || (convertCurrency(Number(row.item_price) * Number(row.item_quantity) - Number(row.coupon_flat_discount || 0)))}</div>}
            </div> */}

            {row.is_addon_order === 1 &&
                //this.props.ActiveTab.slug !== "new" && this.props.ActiveTab.slug !== "confirmed" &&
                <div>
                    {/* <hr /> */}
                    {/* <div className="custom-badge" onClick={() => this.showModificationDetails(this.props.rowInfo)}>Modification Details</div> */}
                    {/* {this.state.showModificationIndex === this.props.rowInfo.index && this.getModifiedHistoryByOrder(row)} */}
                    <CollapsableWidget title="Modification Details" content={this.getModifiedHistoryByOrder(row)} />
                    {row.customization_refund_detail &&
                        Array.isArray(row.customization_refund_detail) &&
                        row.customization_refund_detail.length > 0 &&
                        <CollapsableWidget title="Customization Refunds" content={getRefundsListAndCollectBankDetails(row.customization_refund_detail)} />}
                </div>
            }
            {this.props.isUniversalOrderSearch && userDetails && userDetails.role &&
                userDetails.role.includes("TEAML") &&
                splitOrderStatus.includes(row.orm_order_status) &&
                // splitOrderItemSku.some(sku => row.item_sku.includes(sku)) &&
                Number(row.is_addon_invoice) === 0 &&
                // [0, "0"].includes(row.is_addon_invoice) &&
                row.order_type === 0 &&
                row.item_quantity > 1 &&
                !row.shipment_timestamp &&
                <div>
                    {row.is_addon_order === 0 && <hr />}
                    <div className="custom-badge split-order" onClick={() => this.splitOrder(row)} >Split Order</div>
                </div>
            }

            {/* {this.props.cancelOrderOrCart && <CancelOrderOrCart
                label={"Cancel Cart"}
                rowInfo={row}
                onClick={this.cancelOrder}
            />} */}

            {this.props.isUniversalOrderSearch &&
                row.create_timestamp && new Date(row.create_timestamp) >= new Date('2019-08-15 00:00:00') && new Date(row.create_timestamp) <= new Date('2019-11-22 21:00:00') &&   //  Time period
                (
                    row.price_drop_refund_detail &&
                        Array.isArray(row.price_drop_refund_detail) &&
                        row.price_drop_refund_detail.length > 0
                        ? <CollapsableWidget title="Price Drop Refunds" content={getRefundsListAndCollectBankDetails(row.price_drop_refund_detail)} />
                        : this.props.applyPriceDropRefund &&
                        <div className="custom-badge mt-5" onClick={() => this.applyPriceDropWithContent(row)}>Price Drop Refund</div>
                )
            }

            {row.orm_order_status === 4 &&
                row.cart_cacellation_refund_detail && Array.isArray(row.cart_cacellation_refund_detail) && row.cart_cacellation_refund_detail.length > 0 &&
                <CollapsableWidget title="Cancellation Refunds" content={getRefundsListAndCollectBankDetails(row.cart_cacellation_refund_detail, { isCancelRefunds: true })} />}

            {row.coupon_percentage >= 20 && <div style={{ color: "red", fontWeight: "500" }}>Customer Availed Max Discount</div>}

        </div>
    }
}

export const cartDetailsFilter = (filter, row) => {
    return String(getProductNameBySKU(row._original["item_sku"])).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        String(row._original["item_name"] || "").toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        (row._original["addon_item_sku"] && String(row._original["addon_item_sku"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        (row._original["item_sku"] && String(row._original["item_sku"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        (row._original["item_dimensions"] && String(row._original["item_dimensions"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        (row._original["item_quantity"] && String(row._original["item_quantity"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        (row._original["item_price"] && String(row._original["item_price"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        // String(row._original["item_dimensions"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        // String(row._original["item_quantity"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        // String(row._original["item_price"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        String(row[filter.id]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        String(row._original["delivered_timestamp"] && "Order Delivered").toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())
}

CartDetails.defaultProps = {
    isUniversalOrderSearch: false,
    isReturnModule: false
}

CartDetails.propTypes = {
    rowInfo: PropTypes.object.isRequired,
    userDetails: PropTypes.object.isRequired,
    getMoreModifiedHistoryByOrder: PropTypes.func.isRequired,
    isUniversalOrderSearch: PropTypes.bool,
    cancelOrderOrCart: PropTypes.func,
    applySplitOrder: PropTypes.func
}

export default CartDetails;
