import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Collapse } from '@material-ui/core';

let Wrapper = styled.div`
    margin: 5px 0px;
    border-top: 1px solid #aaa;
    border-bottom: 1px solid #aaa;
`;

let Title = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 700;
    padding: 5px 0px;
`;

let CollapseButton = styled.button`
    background: #3d2277;
    color: white;
    border-radius: 25px;
    width: 20px;
    cursor: pointer;
    border: none;
    padding-right: 16px;
    height: 20px;
`;

let Content = styled.div`
    padding-bottom: 5px;
`;

class CollapsableWidget extends Component {

    state = {
        toggle: false
    }

    componentDidMount() {
        let { defaultOpen } = this.props;
        this.setState({ toggle: defaultOpen });
    }

    handleCollapse = () => this.setState({ toggle: !this.state.toggle });

    render() {
        let { title, content } = this.props;
        let { toggle } = this.state;
        return (
            <Wrapper>
                <Title>{title} <CollapseButton onClick={this.handleCollapse}>{!this.state.toggle ? <i className="fa fa-plus" aria-hidden="true"></i> : <i className="fa fa-minus" aria-hidden="true"></i>}</CollapseButton></Title>
                <Collapse in={toggle}>
                    <Content>
                        {content}
                    </Content>
                </Collapse>
            </Wrapper>
        );
    }
}

CollapsableWidget.defaultProps = {
    title: "Collapse",
    content: "Content",
    defaultOpen: false
}

CollapsableWidget.propTypes = {
    title: PropTypes.string.isRequired,
    content: PropTypes.object.isRequired,
    defaultOpen: PropTypes.bool
}

export default CollapsableWidget;