export const getProductNameBySKU = (sku = "", originalSku = "") => {
    let split_sku = sku.split(/(\d+)/);
    let productName = "";
    // console.log(split_sku, sku);

    if (((split_sku[0] === "WSWB" && !split_sku[split_sku.length - 1].includes("COMBO")) || split_sku[0] === "WFB") && (split_sku.length > 2 && split_sku[2] !== "")) {
        let skuTypeExtension = split_sku[split_sku.length - 1].split('-');
        let selectedType = skuTypes.filter(type => type.sku === skuTypeExtension[0]);
        selectedType = selectedType.length > 0 ? selectedType[0] : {};
        productName = (selectedType.name + (skuTypeExtension && skuTypeExtension.length > 1 ? skuTypeExtension[1] === 'COVER' ? ' - (Cover)' : '' : '')) || "";
    } else {
        let product = products.filter(product => product.key === split_sku[0]);
        product = product.length > 0 ? product[0] : {}
        productName = product.label || "";
    }

    if (split_sku[split_sku.length - 1].includes("COMBO")) {
        productName = productName + " - COMBO";
    }
    if (originalSku.includes("WDCM") && sku.includes("WDCM")) {
        if (originalSku.includes("-COVER")) productName += " - (Cover)";
        else if (originalSku.includes("-ZIPPER")) productName += " - (Zipper)";
    }

    if (sku.toLocaleUpperCase() === "WFSBDCM78605") {
        productName = "Wakefit Sleep Box with sleep accessories and 5 inch Queen High Resilience (HR) Foam Mattress 78 inch SBDCM78605 60 inch";
    } else if (sku.toLocaleUpperCase() === "WFSBDCM78725") {
        productName = "Wakefit Sleep Box with sleep accessories and 5 inch Queen High Resilience (HR) Foam Mattress 78 inch SBDCM78725 72 inch";
    }

    // if (sku.toLocaleUpperCase().includes("WFSBDCM") || sku.toLocaleUpperCase().includes("WFSBDCM")) {
    //     productName = `Wakefit Sleep Box with sleep accessories and ${sku.toLocaleUpperCase().includes('7872') ? "King" : sku.toLocaleUpperCase().includes('7860') ? 'Queen' : ''} ${sku.toLocaleUpperCase().includes('DC') ? 'High Resilience Foam (HR)' : sku.toLocaleUpperCase().includes('MF') ? 'Memory Foam (MF)' : ''} Mattress ${sku}`;
    // }

    // console.log(sku, split_sku, productName);

    return productName // product.label || "";
}

export const getProductById = (id) => {
    return products.filter(product => product.id === id).length > 0 ? products.filter(product => product.id === id)[0] : {};
}

export const getPossibleProductsBySku = (sku) => {
    let extractedSku = sku.split(/(\d+)/)[0];
    let matchedProducts = products
        .filter(product => product.key === extractedSku);
    let possibleProducts = matchedProducts.length > 0 ?
        matchedProducts[0].possibleProductToShow
            .map(id => getProductById(id))
        : [];
    return possibleProducts
}

export const getPossibleProductsForReplacement = (sku) => {
    let matchedProducts = products.filter(product => sku.includes(product.key));
    let possibleProducts = matchedProducts.length > 0 ?
        matchedProducts[0].possibleProductToReplace.map(id => getProductById(id))
        : [];
    // console.log(sku, matchedProducts, possibleProducts);
    return possibleProducts;
}
