export const affiliateTypes = [{
    label: "Wakefit",
    slug: "wakefit",
    id: 0
}, {
    label: "Amazon",
    slug: "amazon",
    id: 1
}, {
    label: "Pepperfry",
    slug: "pepperfry",
    id: 2
}, {
    label: "Flipkart",
    slug: "flipkart",
    id: 3
}];

export const getAffiliates = () => {
    return affiliateTypes;
}

export const getAffiliateById = (id) => {
    return affiliateTypes.filter(affiliate => affiliate.id === id)[0] || {};
}