export const getRoleByUserDetails = (userDetails) => {
    if (userDetails) {
        if (userDetails.role) {
            if (userDetails.role.includes("TEAML")) {
                return "TeamLeader";
            } else if (userDetails.role.includes("CET") || userDetails.role.includes("OC") || userDetails.role.includes("RT") || userDetails.role.includes("Q") || userDetails.role.includes("C")) {
                return "CustomerExperienceTeam";
            } else if (userDetails.role.includes("FINANCE")) {
                return "Finance";
            } else if (userDetails.role.includes("OPS")) {
                return "OperationsTeam";
            } else if (userDetails.role.includes("SALES")) {
                return "SalesTeam";
            } else if (userDetails.role.includes("PARTNERSHIPS")) {
                return "Partnerships";
            } else if (userDetails.role.includes("ADMIN")) {
                return "Admin";
            } else if (userDetails.role.includes("COUPON MANAGER")) {
                return "Coupon manager";
            }
        }
    }
    return "";
}
