import { GET_REFUND_ORDERS_SUCCESS, EDIT_REFUND_ORDERS_SUCCESS, RESET_REFUND_ORDERS_DATA } from "./constants";
import { showLoader, hideLoader } from "../../components/Loader/action";
import handleError from "../../errorHandler";
import { getRefundOrders, editRefundDetails } from "../../api";
import { toast } from "react-toastify";
import { refreshReturnOrders } from "../ReturnsModule/action";
import { refreshOrders } from "../Dashboard/action";

export function getRefundOrdersAction(params) {
    return (dispatch) => {
        dispatch(showLoader());
        getRefundOrders(params)
            .then(response => {
                dispatch(hideLoader());
                if (response.status === 200) {
                    dispatch(getRefundOrdersSuccess(response.data));
                }
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function getRefundOrdersSuccess(data) {
    return {
        type: GET_REFUND_ORDERS_SUCCESS,
        data
    }
}

export function resetRefundDetails() {
    return {
        type: RESET_REFUND_ORDERS_DATA
    }
}

export function editRefundDetailsAction(data) {
    return (dispatch) => {
        let params = { id: data.id };
        dispatch(showLoader());
        editRefundDetails(params, data)
            .then(response => {
                dispatch(hideLoader());
                dispatch(editRefundDetailsSuccess(data));
                window.location.pathname.includes("order-search") && dispatch(refreshOrders());
                window.location.pathname.includes("returns-replacement") && dispatch(refreshReturnOrders());
                // toast.success(response.data.message);
                toast.success("Successfully Updated!");
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function editRefundDetailsSuccess(data) {
    return {
        type: EDIT_REFUND_ORDERS_SUCCESS,
        data
    }
}
