import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { editRefundDetailsAction } from '../../../action';

class CancelRefundRequest extends Component {
    state = {
        refundDetails: {}
    }

    handleCancel = (event) => {
        event.preventDefault();
        let { refundDetails } = this.state;

        refundDetails["id"] = this.props.refund.id;
        refundDetails["refund_primary_id"] = this.props.refund.id;
        refundDetails["refund_status"] = 4;
        refundDetails["refund_comments"] = "";

        this.props.editRefundDetailsAction(refundDetails);
    }

    render() {
        return (
            <div className="custom-badge" onClick={this.handleCancel}>Cancel Refund Request</div>
        )
    }
}


const mapStateToProps = (state, ownProps) => {
    return {}
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ editRefundDetailsAction }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(CancelRefundRequest);
