import React, { Component } from 'react';
import './style.scss';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from '../../../../components/Button';
import { editRefundDetailsAction } from '../../../FinanceModule/action';

class CollectBankDetails extends Component {
    state = {
        refundDetails: {
            account_holder_name: "",
            account_number: "",
            ifsc_code: "",
            bank_name: ""
        }
    }

    handleRefundChange = (event) => {
        let filedName = event.target.name;
        let fieldValue = event.target.value;
        let { refundDetails } = this.state;

        refundDetails[filedName] = fieldValue;

        this.setState({ refundDetails });
    }

    handleSubmit = (event) => {
        event.preventDefault();
        let { refundDetails } = this.state;

        refundDetails["id"] = this.props.id;
        refundDetails["refund_primary_id"] = this.props.id;
        refundDetails["refund_status"] = 1;

        this.props.editRefundDetailsAction(refundDetails);
    }

    render() {
        let { refundDetails } = this.state;
        return (
            <div className="collect-bank-details">
                <div>
                    <input
                        name="account_holder_name"
                        className="input-field"
                        placeholder="Account Holder Name"
                        onChange={this.handleRefundChange}
                        value={refundDetails.account_holder_name}
                    />
                </div>
                <div>
                    <input
                        name="account_number"
                        className="input-field"
                        placeholder="Account Number"
                        onChange={this.handleRefundChange}
                        value={refundDetails.account_number}
                    />
                </div>
                <div>
                    <input
                        name="ifsc_code"
                        className="input-field"
                        placeholder="IFSC Code"
                        onChange={this.handleRefundChange}
                        value={refundDetails.ifsc_code}
                    />
                </div>
                <div>
                    <input
                        name="bank_name"
                        className="input-field"
                        placeholder="Bank Name"
                        onChange={this.handleRefundChange}
                        value={refundDetails.bank_name}
                    />
                </div>
                <div className="button-container">
                    <Button
                        label={"Update"}
                        disabled={!Object.keys(refundDetails).every(key => refundDetails[key] !== "")}
                        onClick={this.handleSubmit}
                    />
                </div>
            </div>
        )
    }
}


const mapStateToProps = (state, ownProps) => {
    return {}
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ editRefundDetailsAction }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(CollectBankDetails);
