import React from 'react';
import { getRefundStatusById, getTroubleReasonById, getRefundCategoryById } from '../../../../../config/refundDetails';
import { getDateTime } from '../../../../../config/time';
import CollectBankDetails from '../../../../Dashboard/components/CollectBankDetails';
import CollectInvalidAmountDetails from '../../../../Dashboard/components/CollectInvalidAmountDetails';
import { convertCurrency } from '../../../../../utilities/commaSeperation';
import CancelRefundRequest from '../CancelRefundRequest';

export const getRefundsListAndCollectBankDetails = (refunds, extras) => {
    return (
        refunds.map((refund, index) => <div key={`refund-details-${index}`}>
            <div className="break-word"><b>Refund Status: </b>{getRefundStatusById(refund.refund_status).label}</div>
            <div className="break-word"><b>Refund Category: </b>{getRefundCategoryById(refund.refund_category).label}</div>
            {Number(refund.refund_status) === 3 && <div className="break-word"><b>Troble Reason: </b>{getTroubleReasonById(refund.refund_trouble_reason).label}</div>}
            {Number(refund.refund_status) === 3 && <div className="break-word"><b>Troble Comments: </b>{refund.trouble_comments}</div>}
            <div className="break-word"><b>Refund Type: </b>{refund.refund_type}</div>
            {refund.reference_id && <div className="break-word"><b>Reference Id: </b>{refund.reference_id}</div>}
            {refund.amount && <div className="break-word"><b>Initiated Amount: </b>{convertCurrency(refund.amount)}</div>}
            <div className="break-word"><b>Comments:</b> {refund.comments}</div>
            <div className="break-word"><b>Created By:</b> {refund.created_by_name}</div>
            <div className="break-word"><b>Created At:</b> {getDateTime(refund.create_timestamp)}</div>
            {refund.refund_amount && <div className="break-word">            <hr />
                <b>Processed Amount: </b> {convertCurrency(refund.refund_amount)}</div>}
            {refund.refund_comments && <div className="break-word"><b>Refund Comments:</b> {refund.refund_comments}</div>}
            {refund.refund_by_name && <div className="break-word"><b>Refund By:</b> {refund.refund_by_name}</div>}
            {refund.refund_timestamp && <div className="break-word"><b>Refund At:</b> {getDateTime(refund.refund_timestamp)}</div>}

            {Number(refund.refund_status) === 3 && [1, 2, 3].includes(Number(refund.refund_trouble_reason)) ? <div>
                <hr />
                {[2, 3].includes(Number(refund.refund_trouble_reason)) ?
                    <CollectBankDetails id={refund.id} /> :
                    [1].includes(Number(refund.refund_trouble_reason)) && <CollectInvalidAmountDetails id={refund.id} />}
            </div> :
                (refund.account_holder_name ||
                    refund.account_number ||
                    refund.ifsc_code ||
                    refund.bank_name) &&
                <div>
                    <hr />
                    {refund.account_holder_name && <div><b>Account Holder Name: </b>{refund.account_holder_name}</div>}
                    {refund.account_number && <div><b>Account Number: </b>{(refund.account_number)}</div>}
                    {refund.ifsc_code && <div><b>IFSC Code: </b>{refund.ifsc_code}</div>}
                    {refund.bank_name && <div><b>Bank Name: </b>{refund.bank_name}</div>}
                </div>}

            {extras &&
                extras.isCancelRefunds &&
                !refund.reference_id &&
                !refund.refund_timestamp &&
                [1, 3].includes(Number(refund.refund_status)) &&
                <CancelRefundRequest refund={refund} />}

            {index < refunds.length - 1 && <hr />}
        </div>)
    );
}