import React, { Component } from 'react';
import './style.scss';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Button from '../../../../components/Button';
import { editRefundDetailsAction } from '../../../FinanceModule/action';
import { decimalRegex } from '../../../../config/regex';
import { FormControl, InputLabel, Input, FormHelperText } from '@material-ui/core';

class CollectInvalidAmountDetails extends Component {
    state = {
        refundDetails: {
            amount: ""
        },
        errors: {
            amount: ""
        }
    }

    handleRefundChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;
        let { refundDetails, errors } = this.state;

        if (fieldName === "amount") {
            if (fieldValue && decimalRegex.test(fieldValue)) {
                refundDetails[fieldName] = fieldValue;
                errors[fieldName] = "";
            } else if (!fieldValue) {
                refundDetails[fieldName] = "";
                errors[fieldName] = "Amount is Required!";
            }
        } else {
            refundDetails[fieldName] = fieldValue;
        }

        this.setState({ refundDetails, errors });
    }

    handleSubmit = (event) => {
        event.preventDefault();
        let { refundDetails } = this.state;

        refundDetails["id"] = this.props.id;
        refundDetails["refund_primary_id"] = this.props.id;
        refundDetails["refund_status"] = 1;

        this.props.editRefundDetailsAction(refundDetails);
    }

    render() {
        let { refundDetails, errors } = this.state;
        return (
            <div className="collect-invalid-amount-details">
                <div><b>Provide New Amount:</b></div>
                <FormControl className="full-width form-control-custom" error={errors.amount}>
                    <InputLabel htmlFor="amount">New Amount</InputLabel>
                    <Input
                        id="left"
                        name="amount"
                        type="amount"
                        variant="filled"
                        required={true}
                        value={refundDetails.amount}
                        onChange={this.handleRefundChange}
                        aria-describedby="amount-text"
                        autoFocus={false}
                    />
                    <FormHelperText className="error-text" id="amount-text">{errors.amount}</FormHelperText>
                </FormControl>
                <div className="button-container">
                    <Button
                        label={"Update"}
                        disabled={!Object.keys(refundDetails).every(key => refundDetails[key] !== "")}
                        onClick={this.handleSubmit}
                    />
                </div>
            </div>
        )
    }
}


const mapStateToProps = (state, ownProps) => {
    return {}
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ editRefundDetailsAction }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(CollectInvalidAmountDetails);
