import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './style.scss';

const possibleCartEditStatus = [1, 2, 3, 5, 6, 14, 18, 19];
// const possibleCartEditRoles = ["OC", "OPS"];

class CustomerDetails extends Component {
    render() {
        let row = this.props.rowInfo.original;
        let { userDetails } = this.props;

        return <div className="customer-detail">
            {userDetails && userDetails.role &&
                (
                    userDetails.role.includes("TEAML") ||
                    (!row.shipment_timestamp &&
                        // possibleCartEditRoles.some((role) => userDetails.role.includes(role)) &&
                        possibleCartEditStatus.includes(row.orm_order_status))
                ) &&
                row.order_type === 0 &&
                <div style={{ float: "right" }}
                    onClick={() => this.props.showPopup({
                        type: "customerEdit",
                        header: "Customer Detail Modification",
                        data: {
                            props: this.props.rowInfo,
                            onSubmit: this.props.updateCustomer
                        }
                    })}
                >
                    <i className="fa fa-pencil-square-o font" aria-hidden="true"></i>
                </div>
            }
            <div>{row.firstname + ' ' + row.lastname}</div>
            <div>{row.email_address}</div>
            <div>{row.mobile_number}</div>
            {row.alternate_mobile_number && <div><b>[A] : </b> {row.alternate_mobile_number}</div>}
            <div><b>Address :</b></div>
            <div className="break-word">{row.address}</div>
            <div className="landmark break-word"><b>(L) : </b> {row.landmark}</div>
            <div className="city break-word"> {row.city}, {row.state} - {row.pincode}</div>
            {row.gst_number && <div className="landmark break-word"><b>GST # : </b> {row.gst_number}</div>}
            {row.gst_organization && <div className="landmark break-word"><b>GST Name : </b> {row.gst_organization}</div>}
            <div className="contact">
                <b>Contact : </b>
                {row.mobile_number && this.props.userDetails &&
                    <div className="customer-direct-contact">
                        <div className="custom-badge"
                            onClick={() => this.props.clickToCall(
                                this.props.userDetails.contact,
                                row.mobile_number,
                                row.id,
                                row.orm_order_status,
                                row.affiliate_id
                            )}
                        >{row.mobile_number}</div>
                        {this.props.isUniversalOrderSearch &&
                            this.props.userDetails &&
                            this.props.userDetails.role &&
                            (this.props.userDetails.role.includes("TEAML") || this.props.userDetails.role.includes("OPS")) &&
                            <span
                                className="call-record-icon"
                                onClick={() => this.props.showPopup({
                                    type: "callRecordings",
                                    header: "Order Call Details",
                                    data: {
                                        props: row.mobile_number
                                    }
                                })}
                            ><i className="fa fa-headphones" aria-hidden="true"></i></span>
                        }
                    </div>
                }
            </div>
            {row.alternate_mobile_number && <div className="contact">
                <b>[A] Number :</b>
                <div className="custom-badge"
                    onClick={() => this.props.clickToCall(
                        this.props.userDetails.contact,
                        row.alternate_mobile_number,
                        row.id,
                        row.orm_order_status,
                        row.affiliate_id
                    )}
                >{row.alternate_mobile_number}</div>

                {this.props.isUniversalOrderSearch &&
                    this.props.userDetails &&
                    this.props.userDetails.role &&
                    (this.props.userDetails.role.includes("TEAML") || this.props.userDetails.role.includes("OPS")) &&
                    <span
                        className="call-record-icon"
                        onClick={() => this.props.showPopup({
                            type: "callRecordings",
                            header: "Order Call Details",
                            data: {
                                props: row.alternate_mobile_number
                            }
                        })}
                    ><i className="fa fa-headphones" aria-hidden="true"></i></span>
                }
            </div>}
        </div>
    }
}

export const customerDetailFilter = (filter, row) => {
    return String(row._original["firstname"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        (row._original["lastname"] && String(row._original["lastname"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        String(row._original["email_address"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        String(row._original["mobile_number"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        (row._original["alternate_mobile_number"] && String(row._original["alternate_mobile_number"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        String(row._original["address"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        (row._original["landmark"] && String(row._original["landmark"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        (row._original["city"] && String(row._original["city"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())) ||
        String(row._original["state"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        String(row._original["pincode"]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase()) ||
        String(row[filter.id]).toLocaleLowerCase().includes(filter.value.toLocaleLowerCase())
}

CustomerDetails.defaultProps = {
    isUniversalOrderSearch: false,
    type: "button"
}

CustomerDetails.propTypes = {
    rowInfo: PropTypes.object.isRequired,
    userDetails: PropTypes.object,
    clickToCall: PropTypes.func.isRequired,
    showPopup: PropTypes.func,
    isUniversalOrderSearch: PropTypes.bool,
    updateCustomer: PropTypes.func
}

export default CustomerDetails;
