const refundStatuses = [{
    id: 1,
    label: "Refund Initiated",
    is_active: 1
}, {
    id: 2,
    label: "Refund Processed",
    is_active: 1
}, {
    id: 3,
    label: "Refund Trouble",
    is_active: 1
}, {
    id: 4,
    label: "Refund Cancelled",
    is_active: 1
}];

export const getRefundCategories = () => {
    return refundCategories;
}

export const getRefundCategoryById = (id) => {
    let category = refundCategories.filter(category => category.id === id);
    category = category.length > 0 ? category[0] : {};
    return category;
}

export const getRefundStatuses = () => {
    return refundStatuses;
}

export const getRefundStatusById = (id) => {
    let reason = refundStatuses.filter(reason => reason.id === id);
    reason = reason.length > 0 ? reason[0] : {};
    return reason;
}

export const getTroubleReasons = () => {
    return troubleReasons;
}

export const getTroubleReasonById = (id) => {
    let reason = troubleReasons.filter(reason => reason.id === id);
    reason = reason.length > 0 ? reason[0] : {};
    return reason;
}
