export function submitAccessoryRequest(data) {
    return (dispatch) => {
        dispatch(showLoader());
        updateReturnResolution(data)
            .then(response => {
                dispatch(hideLoader());
                if (response.status === 200) {
                    toast.success("Successfully Issued!");
                    // toast.success(response.data.message);
                    dispatch(issueAccessorySuccess(response.data));
                }
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function issueAccessorySuccess(data) {
    return {
        type: ISSUE_ACCESSORY_SUCCESS,
        data
    }
}

export function initateRefundAction(data) {
    return (dispatch) => {
        dispatch(showLoader());
        initateRefund(data)
            .then(response => {
                dispatch(hideLoader());
                toast.success("Refund Initiated!");
                // toast.success(response.data.message);
                dispatch(intiateRefundSuccess(response.data));
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function intiateRefundSuccess(data) {
    return {
        type: INTIATE_REFUND_SUCCESS,
        data
    }
}

export function refreshReturnOrders(data) {
    return {
        type: REFRESH_ORDERS,
        data
    }
}
